import React from "react";
import Fade from "react-reveal/Fade";

import { InputText, InputFile } from "elements/Form";

import logoBca from "assets/images/logo-bca.jpg";
import logoMandiri from "assets/images/logo-mandiri.jpg";

export default function Payment(props) {
  const { data, ItemDetails, checkout, page } = props;

  let element = {};
  for (const key in page) {
    if (Object.hasOwnProperty.call(page, key)) {
      let e = page[key];
      element = { data: e };
    }
  }
  element = { ...element.data };
  console.log("element===>", element);
  const tax = 10;
  const subTotal = ItemDetails.price * checkout.duration;
  const grandTotal = (subTotal * tax) / 100 + subTotal;

  return (
    <Fade>
      <div className="container" style={{ marginBottom: 30 }}>
        <div className="row justify-content-center align-items-center">
          <div className="col-5 border-right py-5" style={{ paddingRight: 80 }}>
            <Fade delay={300}>
              <p className="mb-4">Transfer Pembayaran:</p>
              <p>Tax: {tax}%</p>
              <p>
                Sub total: {subTotal}K / {checkout.duration} {ItemDetails.unit}
                {+checkout.duration > 1 ? "s" : ""}
              </p>
              <p>Total: {grandTotal}K</p>
              <p className="mt-4">Bank :</p>
              {element.bank.map((e, key) => (
                <p key={key}>
                  {/* <div className="col-3 text-right"> */}
                  {/* <img
                      alt={e.nameBank}
                      src={"assets/" + e.imageUrl}
                      width="60"
                    /> */}
                  {/* </div> */}
                  <p>
                    {e.nameBank}: {e.nomorRekening}
                  </p>
                </p>
              ))}

              {/* <div className="row">
                <div className="col-3 text-right">
                  <img src={logoMandiri} alt="mandiri" width="60" />
                </div>
                <div className="col">
                  <dl>
                    <dd>Bank BCA</dd>
                    <dd>0000 1235</dd>
                    <dd>KalbarVacRek</dd>
                  </dl>
                </div>
              </div> */}
            </Fade>
          </div>
          <div className="col-5 py-5" style={{ paddingLeft: 80 }}>
            <Fade delay={600}>
              <label htmlFor="proofPayment">Upload Bukti Transfer</label>
              <InputFile
                accept="image/*"
                id="proofPayment"
                name="proofPayment"
                value={data.proofPayment}
                onChange={props.onChange}
              />

              <label htmlFor="bankName">Asal Bank</label>
              <InputText
                id="bankName"
                name="bankName"
                type="text"
                value={data.bankName}
                onChange={props.onChange}
              />

              <label htmlFor="bankHolder">Nama Pengirim</label>
              <InputText
                id="bankHolder"
                name="bankHolder"
                type="text"
                value={data.bankHolder}
                onChange={props.onChange}
              />
            </Fade>
          </div>
        </div>
      </div>
    </Fade>
  );
}
