import React, { useState, useEffect } from "react";
import { Link, useHistory, useLocation } from "react-router-dom";
import { useDispatch } from "react-redux";
import Fade from "react-reveal/Fade";
import decode from "jwt-decode";
import { LOGOUT } from "../store/types";

import Button from "elements/Button";
import BrandIcon from "parts/IconText";

const Header = (props) => {
  const [user, setUser] = useState(JSON.parse(localStorage.getItem("profile")));
  const dispatch = useDispatch();
  const location = useLocation();
  const history = useHistory();

  const logout = () => {
    dispatch({ type: LOGOUT });
    history.push("/auth");
    setUser(null);
  };

  useEffect(() => {
    const token = user?.token;
    if (token) {
      console.log("token==", token);
    } else {
      console.log("token tidak ada");
    }

    if (token) {
      const decodedToken = decode(token);
      if (decodedToken.exp * 1000 < new Date().getTime()) logout();
    }
    setUser(JSON.parse(localStorage.getItem("profile")));
  }, [location]);

  // export default function Header(props) {
  const getNavLinkClass = (path) => {
    return props.location.pathname === path ? " active" : "";
  };

  if (props.isCentered)
    return (
      <Fade>
        <header className="spacing-sm">
          <div className="container">
            <nav className="navbar navbar-expand-lg navbar-light">
              <Button className="brand-text-icon mx-auto" href="" type="link">
                Kalbar<span className="text-gray-900">Vacation</span>
              </Button>
            </nav>
          </div>
        </header>
      </Fade>
    );

  return (
    <Fade>
      <header className="spacing-sm">
        <div className="container">
          <nav className="navbar navbar-expand-lg navbar-light">
            <BrandIcon />
            <div className="collapse navbar-collapse">
              <ul className="navbar-nav ml-auto">
                <li className={`nav-item${getNavLinkClass("/")}`}>
                  <Button className="nav-link" type="link" href="/">
                    Home
                  </Button>
                </li>
                <li className={`nav-item${getNavLinkClass("/")} dropdown`}>
                  {user?.result ? (
                    <div className="navbar-nav mr-auto">
                      <img
                        className="img rounded mx-auto d-block"
                        style={{ height: 40 }}
                        src={user?.result.file || user?.result.imageUrl}
                      />
                      <span className="navbar-nav mr-5">
                        Welcome, {user?.result.username}
                      </span>
                      <button
                        style={{ height: 5 }}
                        className="btn btn-outline-danger btn-sm"
                        onClick={logout}
                      >
                        Logout
                      </button>
                    </div>
                  ) : (
                    <Button className="nav-link" type="link" href="/auth">
                      Sign In{" "}
                    </Button>
                  )}
                </li>
              </ul>
            </div>
          </nav>
        </div>
      </header>
    </Fade>
  );
};

export default Header;
